import React from 'react'
import { Link } from 'react-router-dom'

const NavigationBar = () => {
    return (
        <div className="sticky-top">
            <nav className="flex items-center justify-between flex-wrap bg-teal-500 px-6 py-6 scroll-m-3">
                <div className="flex items-center flex-shrink-0 text-white mr-6">
                    <svg className="fill-current h-8 w-8 mr-2" width="54" height="54" viewBox="0 0 54 54" xmlns="http://www.w3.org/2000/svg"><path d="M13.5 22.1c1.8-7.2 6.3-10.8 13.5-10.8 10.8 0 12.15 8.1 17.55 9.45 3.6.9 6.75-.45 9.45-4.05-1.8 7.2-6.3 10.8-13.5 10.8-10.8 0-12.15-8.1-17.55-9.45-3.6-.9-6.75.45-9.45 4.05zM0 38.3c1.8-7.2 6.3-10.8 13.5-10.8 10.8 0 12.15 8.1 17.55 9.45 3.6.9 6.75-.45 9.45-4.05-1.8 7.2-6.3 10.8-13.5 10.8-10.8 0-12.15-8.1-17.55-9.45-3.6-.9-6.75.45-9.45 4.05z"/></svg>
                    <span className="font-semibold text-xl tracking-tight">NAKUL DEB</span>
                </div>
                <div className="block lg:hidden">
                    <button className="flex items-center px-3 py-2 border rounded text-teal-200 border-teal-400 hover:text-white hover:border-white" id='menu' type="button" data-bs-toggle="collapse" data-bs-target="#collapseMenu" aria-expanded="true" aria-controls="collapseMenu">
                        <svg className="fill-current h-3 w-3" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><title>Menu</title><path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z"/></svg>
                    </button>
                </div>
                <div className="w-full block flex-grow lg:flex lg:items-center lg:w-auto accordion-collapse collapse lg-must-show" id="collapseMenu" aria-labelledby="menu">
                    <div className="text-sm lg:flex-grow  lg:flex lg:justify-end">
                        <Link to="/" className="block mt-4 lg:inline-block lg:mt-0 text-gray-50 hover:text-emerald-800 hover:scale-110 mr-4 font-bold text-lg md:text-lg">Home</Link>
                        <Link to="/contact" className="block mt-4 lg:inline-block lg:mt-0 text-gray-50 hover:text-emerald-800 hover:scale-110 mr-4 font-bold text-lg md:text-lg">Contact</Link>
                        <Link to="/about" className="block mt-4 lg:inline-block lg:mt-0 text-gray-50 hover:text-emerald-800 hover:scale-110 mr-4 font-bold text-lg md:text-lg">About</Link>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default NavigationBar